<template>
    <div class="wx-page">
        <scroller class="scroller">
            <div class="text-content">
                <text class="text"></text>
            </div>
            <wxc-searchbar></wxc-searchbar>
            <wxc-cell label=""
                      :title="currentDate"
                      :has-arrow="true"
                      @wxcCellClicked="dateChooseClicked"></wxc-cell>

            <wxc-cell label=""
                      :has-arrow="true"
                      @wxcCellClicked="moreChooseClicked"></wxc-cell>

            <wxc-cell label="" :has-arrow="false">
                <switch slot="value"></switch>
            </wxc-cell>

            <div class="item-button">
                <wxc-button type="yellow"
                            text=""
                            @wxcButtonClicked="showLoading"></wxc-button>
            </div>

            <div class="item-button">
                <wxc-button type="red"
                            text=""
                            @wxcButtonClicked="showError"></wxc-button>
            </div>

        </scroller>
        <wxc-popup width="500"
                   pos="right"
                   :show="isShowPop"
                   @wxcPopupOverlayClicked="overlayClicked">
            <text class="more-text"></text>
        </wxc-popup>
        <wxc-page-calendar ref="wxcPageCalendar"
                           :date-range="dateRange"
                           :selected-date="selectedDate"
                           :selected-note="selectedNote"
                           :minibar-cfg="minibarCfg"
                           :desc-list="descList"
                           @wxcPageCalendarDateSelected="pageCalendarDateSelected"
        ></wxc-page-calendar>
        <wxc-mask height="800"
                  width="702"
                  :show="isShowMask"
                  @wxcMaskSetHidden="maskSetHidden">
            <scroller class="content">
                <text class="mask-title"></text>
                <text class="mask-content">:
                    17:00</text>
                <text class="mask-content">:
                    142823
                    ICOCA</text>
            </scroller>
        </wxc-mask>
        <wxc-result type="errorPage"
                    :show="isShowError"
                    @wxcResultButtonClicked="resultButtonClicked">
        </wxc-result>
        <wxc-loading :show="isShowLoading"
                     loading-text=""></wxc-loading>
    </div>
</template>

<script>
  import {
    WxcSearchbar,
    WxcButton,
    WxcPopup,
    WxcCell,
    WxcResult,
    WxcPageCalendar,
    WxcIndexlist,
    WxcLoading,
    WxcMask
  } from 'weex-ui';
  const modal = weex.requireModule('modal');
  module.exports = {
    components: {
      WxcSearchbar,
      WxcButton,
      WxcPopup,
      WxcCell,
      WxcResult,
      WxcPageCalendar,
      WxcIndexlist,
      WxcLoading,
      WxcMask
    },
    data: () => ({
      isShowPop: false,
      isShowError: false,
      isShowLoading: false,
      isShowMask: false,
      currentDate: '2017-01-20',
      selectedDate: ['2017-01-20'],
      calendarTitle: '',
      dateRange: ['2018-01-01', '2018-04-30'],
      selectedNote: [''],
      minibarCfg: {
        title: '',
        backgroundColor: "#EEEEEE"
      },
      descList: [
        { date: '2017-12-23', value: '200' },
        { date: '2017-12-24', value: '200' },
        { date: '2017-12-25', value: '200' },
        { date: '2017-12-26', value: '200' },
        { date: '2017-12-27', value: '222' },
        { date: '2017-12-28', value: '341' },
        { date: '2017-12-29', value: '230' },
        { date: '2017-12-06', value: '2000', emphasize: true }
      ]
    }),
    methods: {
      moreChooseClicked () {
        this.isShowPop = true;
      },
      overlayClicked () {
        this.isShowPop = false;
      },
      cellClicked () {
        modal.toast({
          'message': 'wxcCellClicked',
          'duration': 1
        })
      },
      showError () {
        this.isShowError = true;
      },
      showLoading () {
        this.isShowLoading = true;
        setTimeout(() => {
          this.isShowLoading = false;
          this.isShowMask = true;
        }, 1000);
      },
      resultButtonClicked () {
        this.isShowError = false;
      },
      dateChooseClicked (e) {
        setTimeout(() => {
          this.$refs['wxcPageCalendar'].show();
        }, 1);
      },
      pageCalendarDateSelected (e) {
        this.selectedDate = e.date;
        this.currentDate = e.date[0];
      },
      maskSetHidden () {
        this.isShowMask = false;
      }
    }
  };
</script>

<style scoped>
    .wx-page {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .scroller {
        flex: 1;
    }
    .item-button {
        margin-top: 40px;
        align-items: center;
    }
    .content{
        height: 800px;
    }
    .text-content {
        height: 140px;
        margin-top: 40px;
        margin-bottom: 10px;
        align-items: center;
        justify-content: center;
    }
    .text {
        font-size: 40px;
        color: #333333;
    }
    .more-text {
        font-size: 30px;
        margin-top: 140px;
        margin-left: 80px;
    }
    .mask-title {
        font-size: 40px;
        margin-top: 40px;
        margin-bottom: 30px;
        align-items: center;
        margin-left: 40px;
    }
    .mask-content {
        padding-top: 10px;
        padding-left: 40px;
        padding-right: 40px;
        font-size: 30px;
    }
</style>



